import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { useTheme } from "@react-navigation/native";
import { styles } from "./Style";

const PartOfSpeech = ({ navigation }) => {
  const { colors } = useTheme();
  return (
    <ScrollView>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Part Of Speech</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          All the english words are divided into eight groups each groups are called part of speech. {"\n"} {"\n"}
          (1) Nouns  {"\n"}
          (2) Pronoun  {"\n"}
          (3) Adjectives  {"\n"}
          (4) Verb  {"\n"}
          (5) Adverb  {"\n"}
          (6) Prepositions  {"\n"}
          (7) Conjunctions  {"\n"}
          (8) Interjections  {"\n"}
          
        </Text>
      </View>

      {/* <TouchableOpacity
        activeOpacity={0.8}
        onPress={() => navigation.navigate("PartOfSpeech")}
      >
        <View style={styles.nextCon}>
          <Text style={styles.next}>PartOfSpeech</Text>
        </View>
      </TouchableOpacity> */}
    </ScrollView>
  );
};

export default PartOfSpeech;

