import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { useTheme } from "@react-navigation/native";
import { styles } from "./Style";

const Noun = ({ navigation }) => {
  const { colors } = useTheme();
  return (
    <ScrollView>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Noun</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A Noun is the name of person, animal, place or things {"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>Example : </Text> Ram, Shyam,
          Delhi, Bihar, cow, dog. etc. {"\n"} {"\n"}
          There are five types of nouns: {"\n"} {"\n"}
          1. Proper Noun {"\n"}
          2. Common Noun {"\n"}
          3. Collective Noun {"\n"}
          4. Material Noun {"\n"}
          5. Abstract Noun {"\n"}
        </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Proper Noun</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A proper noun is the name of particular person, animal, place or
          thing. {"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>Example : </Text> Rohan, Maya,
          Tommy, Doggy, Patna, India, ...etc.
        </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Common Noun</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A common noun is the name of person, animal, place or thing that we
          used commonly{"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>Example : </Text> Boy, girl, dog,
          cat, state, country, book, pen ....etc.
        </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Collective Noun</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A collective noun is the collection or group of persons or things.
          {"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>Example : </Text> Army, class,
          croud, team, family ...etc.
        </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Material Noun</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A material noun is the name of material substance that we can not
          count.{"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>Example : </Text> Milk, water,
          oil, rice, sugar, rice, salt, curd, ...etc.
        </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Abstract Noun</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          An abstract noun is the name of thing that we can't see and touch only
          we can feel.{"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>Example : </Text> Good, bad, sad
          , happy, honesty, heat, cold, ...etc. {"\n"} {"\n"}
          Science and Arts subjects like Physics, Chemistry, Biology, music,
          history, grammar, etc. are abstract nouns.
        </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Modern Classification of Noun</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          There are three nouns according to the modern classification of noun.{" "}
          {"\n"} {"\n"}
          1. Proper Noun {"\n"}
          2. Countable Noun {"\n"}
          3. Uncountable Noun {"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>Countable Noun : </Text> A noun
          that we can count , are called countable noun. {"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>Example : </Text> Cow, dog, cat,
          sheep, horse, book, pen , home, ...etc. {"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>Uncountable Noun : </Text> A noun
          that we can not count, are called uncountable noun. {"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>Example : </Text> Water, milk,
          rice, butter ...etc.
        </Text>
      </View>
      {/* <TouchableOpacity
        activeOpacity={0.8}
        onPress={() => navigation.navigate("Noun")}
      >
        <View style={styles.nextCon}>
          <Text style={styles.next}>Noun</Text>
        </View>
      </TouchableOpacity> */}
    </ScrollView>
  );
};

export default Noun;
