import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { useTheme } from "@react-navigation/native";
import { styles } from "./Style";

const Verb = ({ navigation }) => {
  const { colors } = useTheme();
  return (
    <ScrollView>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Verbs</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A verb is a word used to express an action, relation, possession and a state of being. {"\n"} {"\n"}
          <Text style={{ fontWeight: 'bold' }}>Example : </Text> Children <Text style={{ fontWeight: 'bold' }}>run</Text>. {"\n"}                   He <Text style={{ fontWeight: 'bold' }}>is walking</Text>. {"\n"}                   I <Text style={{ fontWeight: 'bold' }}>have</Text> a collage degree. {"\n"} {"\n"}
          <Text style={{ fontWeight: 'bold' }}>Run, is, walking, have</Text> are verbs. {"\n"} {"\n"}
          Some popular verbs are <Text style={{ fontWeight: '500' }}>Play, read, learn, talk, teach, am, is, are, was, were, shall, will, have, has, had ....etc.</Text>
        </Text>
      </View>
    </ScrollView>
  );
};

export default Verb;
