import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { useTheme } from "@react-navigation/native";
import { styles } from "./Style";

const Words = ({ navigation }) => {
  const { colors } = useTheme();
  return (
    <ScrollView>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Words</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          Group of letters that give meaning is called word. {"\n"} {"\n"}
          <Text style={{ fontWeight: 'bold' }}>Example : </Text>A, P, L, E is letters and if we combine all these letters
          then we get a word called APPLE. {"\n"} {"\n"}Apple is a words because
          this its have meaning. {"\n"} {"\n"}
          Dog, Cat, Mouse, Rat, Elephant, Tiger, Lion, Monkey .... etc are
          called words.
        </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Exercise Time</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          1. Make a words using following letters. {"\n"} {"\n"}
          (i) A, P, L, E, P (ii) D, G, O (iii) R, T, A {"\n"} {"\n"}
          2. Write all letters that involved in following words. {"\n"} {"\n"}
          (i) Mango (ii) Donkey (iii) Honest
        </Text>
      </View>

      {/* <TouchableOpacity
        activeOpacity={0.8}
        onPress={() => navigation.navigate("Words")}
      >
        <View style={styles.nextCon}>
          <Text style={styles.next}>Words</Text>
        </View>
      </TouchableOpacity> */}
    </ScrollView>
  );
};

export default Words;