import { StatusBar } from "expo-status-bar";
import React, { useState } from "react";
import { TouchableOpacity, View, Text, useColorScheme } from "react-native";
import {
  NavigationContainer,
  DefaultTheme,
  DarkTheme,
} from "@react-navigation/native";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import Home from "./Components/Home";
import Basic from "./Components/Basic";
import Advanced from "./Components/Advanced";
import Quiz from "./Components/Quiz";
import About from "./Components/About";
import Letter from "./Components/Basic/Letter";
import Words from "./Components/Basic/Words";
import Sentences from "./Components/Basic/Sentences";
import PartOfSpeech from "./Components/Basic/PartOfSpeech";
import Noun from "./Components/Basic/Noun";
import Pronoun from "./Components/Basic/Pronoun";
import Adjective from "./Components/Basic/Adjective";
import Verb from "./Components/Basic/Verb";
import Adverb from "./Components/Basic/Adverb";
import Preposition from "./Components/Basic/Preposition";
import Conjunction from "./Components/Basic/Conjunction";
import Interjection from "./Components/Basic/Interjection";
import NumberGrammer from "./Components/Basic/NumberGrammer";
import Person from "./Components/Basic/Person";
import Tense from "./Components/Basic/Tense";
import Gender from "./Components/Basic/Gender"


// theme for dark mode
const MyTheme = {
  dark: false,
  colors: {
    primary: '#171717',
    background: '#171717',
    card: '#202124',
    text: '#bbbfc2',
    border: 'rgb(199, 199, 204)',
    notification: 'rgb(255, 69, 58)',
  },
};

const Stack = createNativeStackNavigator();
// white color for background
const Wcolor = () => "white";
// main color for background
const MainColor = () => "#ff4e5a";

// change light to dark and dark to light changing the ui of the app
const LightThemeText = () => "light";
const DarkThemeText = () => "dark";

export default function App() {
  const [color, setcolor] = useState(Wcolor());
  const [color2, setcolor2] = useState(MainColor());
  const scheme = useColorScheme();
  const [Light, setLight] = useState(DarkThemeText());

  // Stack navigator header style function
  const StackHeaderStyle = () => {
    return (
      <View
        style={{
          borderColor: "white",
          borderWidth: 1,
          flexDirection: "row",
          borderRadius: 50,
        }}
      >
        <TouchableOpacity
          onPress={() => {
            setcolor(MainColor());
            setcolor2(Wcolor());
            setLight(LightThemeText());
          }}
        >
          <View
            style={{
              backgroundColor: color,
              padding: 5,
              borderRadius: 100,
              transform: [{ scale: 0.8 }],
            }}
          >
            <Text style={{ color: "#ff4e5a", fontWeight: "500" }}>On</Text>
          </View>
        </TouchableOpacity>
        <TouchableOpacity
          onPress={() => {
            setcolor(Wcolor());
            setcolor2(MainColor());
            setLight(DarkThemeText());
          }}
        >
          <View
            style={{
              backgroundColor: color2,
              padding: 5,
              borderRadius: 100,
              transform: [{ scale: 0.8 }],
            }}
          >
            <Text style={{ color: "#ff4e5a", fontWeight: "500" }}>Off</Text>
          </View>
        </TouchableOpacity>
      </View>
    );
  };

  return (
    <NavigationContainer theme={scheme === Light ? MyTheme : DefaultTheme}>
      <StatusBar />
      <Stack.Navigator initialRouteName="Home">
        <Stack.Screen
          name="Home"
          component={Home}
          options={{ headerShown: false }}
        />
        <Stack.Screen
          name="Basic"
          component={Basic}
          options={{
            headerTintColor: "white",
            headerStyle: { backgroundColor: "#ff4e5a" },
            headerRight: () => StackHeaderStyle(),
          }}
        />
        <Stack.Screen
          name="Advanced"
          component={Advanced}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
        <Stack.Screen
          name="Quiz"
          component={Quiz}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => (
              <View
                style={{
                  borderColor: "white",
                  borderWidth: 1,
                  flexDirection: "row",
                  borderRadius: 50,
                }}
              >
                <TouchableOpacity
                  onPress={() => {
                    setcolor(MainColor());
                    setcolor2(Wcolor());
                    setLight(LightThemeText());
                  }}
                >
                  <View
                    style={{
                      backgroundColor: color,
                      padding: 5,
                      borderRadius: 100,
                      transform: [{ scale: 0.8 }],
                    }}
                  >
                    <Text style={{ color: "#ff4e5a", fontWeight: "500" }}>
                      On
                    </Text>
                  </View>
                </TouchableOpacity>
                <TouchableOpacity
                  onPress={() => {
                    setcolor(Wcolor());
                    setcolor2(MainColor());
                    setLight(DarkThemeText());
                  }}
                >
                  <View
                    style={{
                      backgroundColor: color2,
                      padding: 5,
                      borderRadius: 100,
                      transform: [{ scale: 0.8 }],
                    }}
                  >
                    <Text style={{ color: "#ff4e5a", fontWeight: "500" }}>
                      Off
                    </Text>
                  </View>
                </TouchableOpacity>
              </View>
            ),
          }}
        />
        <Stack.Screen
          name="About"
          component={About}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
        <Stack.Screen
          name="Letter"
          component={Letter}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Words"
          component={Words}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Sentences"
          component={Sentences}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Part Of Speech"
          component={PartOfSpeech}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Noun"
          component={Noun}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Pronouns"
          component={Pronoun}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Adjective"
          component={Adjective}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Verb"
          component={Verb}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Adverb"
          component={Adverb}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Preposition"
          component={Preposition}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Conjunction"
          component={Conjunction}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Interjection"
          component={Interjection}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Number"
          component={NumberGrammer}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Gender"
          component={Gender}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Person"
          component={Person}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
                <Stack.Screen
          name="Tense"
          component={Tense}
          options={{
            headerTintColor: "white",
            headerStyle: {
              backgroundColor: "#ff4e5a",
            },
            headerRight: () => StackHeaderStyle(),
          }}
        />
      </Stack.Navigator>
    </NavigationContainer>
  );
}
