import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { useTheme } from "@react-navigation/native";
import { styles } from "./Style";

const Conjunction = ({ navigation }) => {
  const { colors } = useTheme();
  return (
    <ScrollView>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Conjunctions</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A conjunction is a word used to join words, sentences together.{"\n"} {"\n"}
          <Text style={{ fontWeight: 'bold' }}>Example : </Text>I <Text style={{ fontWeight: 'bold' }}>and</Text> you are going to {'\n'}                  market.{"\n"}                  You get this money <Text style={{ fontWeight: 'bold' }}>if</Text> you {"\n"}                  do this work. {"\n"} {"\n"}
          <Text style={{ fontWeight: 'bold' }}>And and if </Text>are conjunctions. {"\n"} {"\n"}
          Some popular Conjunction are <Text style={{ fontWeight: '500' }}>and, both, or, because, therefore, although, though, yet, still, as well as, while, as if, whether, where, so, that, until, till, either, neither..etc.  </Text>
        </Text>
      </View>
    </ScrollView>
  );
};

export default Conjunction;
