import { StyleSheet, Text, View, Image,ScrollView, TouchableOpacity} from "react-native";

export default function Home({navigation}) {
  return (
    <ScrollView style={{backgroundColor:'white'}}>
      <View style={styles.topHeading}>
        <Text style={{ fontSize: 35, fontWeight: "900", color: "#ff4e5a", }}>
          Learn Grammer
        </Text>
        <Text>Let's Learn English Grammer with me </Text>
      </View>
      <View
        style={{
          backgroundColor: "#ff4e5a",
          height: 530,
          width: "100%",
          borderTopRightRadius: 50,
          borderTopLeftRadius: 50,
          alignItems: "center",

        }}
      >
        {/* all category */}
        <View
          style={{
            height: "auto",
            width: "100%",
            marginTop: 50,
            flexDirection: "row",
            justifyContent: "center",
            alignItems: "center",
            flexWrap: "wrap",
            transform:[{scale:0.9}],
          }}
        >
          <TouchableOpacity activeOpacity={0.92} onPress={() => navigation.navigate("Basic")}><View style={styles.category}><Text style={{fontSize:30, fontWeight:'900', color:'#ff4e5a'}}>Basic</Text></View></TouchableOpacity>
          <TouchableOpacity activeOpacity={0.92} onPress={() => navigation.navigate("Advanced")}><View style={styles.category}><Text style={{fontSize:30, fontWeight:'900', color:'#ff4e5a'}}>Advanced</Text></View></TouchableOpacity>
          <TouchableOpacity activeOpacity={0.92} onPress={() => navigation.navigate("Quiz")}><View style={styles.category}><Text style={{fontSize:30, fontWeight:'900', color:'#ff4e5a'}}>Quiz</Text></View></TouchableOpacity>
          <TouchableOpacity activeOpacity={0.92} onPress={() => navigation.navigate("About")}><View style={styles.category}><Text style={{fontSize:30, fontWeight:'900', color:'#ff4e5a'}}>About</Text></View></TouchableOpacity>
        </View>

        {/* image for best look and feel */}
        <View
          style={{
            width: "100%",
            height: 250,
            borderTopRightRadius: 50,
            borderTopLeftRadius: 50,
            marginTop: 30,
            backgroundColor: "white",
          }}
        >
          <Image
            source={{
              uri: "https://img.freepik.com/free-vector/exams-concept-illustration_114360-1655.jpg?w=996&t=st=1654842146~exp=1654842746~hmac=ed2a0fc11ea5ac4ceb1986465300ba401084c2d54fedd2d62513845b05b95277",
            }}
            style={{
              width: "100%",
              height: "100%",
              resizeMode: "contain",
              borderTopRightRadius: 50,
              borderTopLeftRadius: 50,
              marginTop: 30,
            }}
          />
        </View>
      </View>
    </ScrollView>
  );
}

const styles = StyleSheet.create({
  topHeading: {
    height: 300,
    width: "100%",
    justifyContent: "center",
    alignItems: "center",
  },
  category: {
    height: 100,
    width: 200,
    backgroundColor: "white",
    borderRadius: 30,
    transform: [{ scale: 0.95 }],
    justifyContent:"center",
    alignItems:'center'
  },
});
