import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { useTheme } from "@react-navigation/native";
import { styles } from "./Style";

const Preposition = ({ navigation }) => {
  const { colors } = useTheme();
  return (
    <ScrollView>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Prepositions</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A preposition is a word that show the position of a noun or a pronoun. {"\n"} {"\n"}
          <Text style={{ fontWeight: 'bold' }}>Example : </Text> This pen is <Text style={{ fontWeight: 'bold' }}>in</Text> my hand. {"\n"}                   The dog is walking <Text style={{ fontWeight: 'bold' }}>on</Text> {"\n"}                   the grass.{'\n'}                   You are siting <Text style={{ fontWeight: 'bold' }}>between</Text> me {"\n"}                   and my sister. {"\n"} {"\n"}
          <Text style={{ fontWeight: 'bold' }}>In, on, between</Text> are prepositions. {"\n"} {"\n"}
          Some popular preposition are <Text style={{ fontWeight: '500' }}>in, into, on, at, to, with, without, of, among, under, over, below, near, before, after, for, form, by, about..etc.</Text>
        </Text>
      </View>
    </ScrollView>
  );
};

export default Preposition;
