import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { useTheme } from "@react-navigation/native";
import { styles } from "./Style";

const Tense = ({ navigation }) => {
  const { colors } = useTheme();
  return (
    <ScrollView>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Tense</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          The tense show the time relation between verb form.   {"\n"}  {"\n"} 
          <Text style={{ fontWeight: 'bold' }}>There are three types of Tese.</Text> {"\n"}  {"\n"}
          1. Present Tense {"\n"}
          2. Past Tense{"\n"}
          3. Future Tense {"\n"}{"\n"}

            <Text style={{ fontWeight: 'bold' }}>Present Tense : </Text> Present tense means what happening in the present time. {"\n"}  {"\n"}
            <Text style={{ fontWeight: 'bold' }}>Example : </Text> I am eating. {"\n"}                   We are running.  {"\n"}  {"\n"} 
            <Text style={{ fontWeight: 'bold' }}>Past Tense  : </Text> Past tense means what happened in the past time. {"\n"}  {"\n"}
            <Text style={{ fontWeight: 'bold' }}>Example : </Text> I was eating. {"\n"}                   We were running.  {"\n"}  {"\n"}
            <Text style={{ fontWeight: 'bold' }}>Future Tense : </Text> Future tense means what will happen in the future time. {"\n"}  {"\n"}
            <Text style={{ fontWeight: 'bold' }}>Example : </Text>I will be an engineer. {"\n"}                   I shall start my business.  {"\n"}  {"\n"}
          </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Present Tense</Text>
        <Text style={[styles.text, { color: colors.text }]}>
      <Text style={{ fontWeight: 'bold' }}>Types of Present Tense. </Text> {"\n"}  {"\n"}
      1. Simple Present Tense {"\n"}
      2. Present Continuous Tense {"\n"}
      3. Present Perfect Tense {"\n"}
      4. Present Perfect Continuous Tense {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>1. Simple Present Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure  : </Text> Subject + verb in simple present form(eather singular or plural) + object. {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I am a boy. {"\n"}                  We are sturdets. {"\n"}                  She has a dog. {"\n"}                  The cow eats grass. etc. {"\n"}{"\n"} 
      <Text style={{ fontWeight: 'bold' }}>2. Present Continuous Tense or {"\n"}Present Progressive Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure : </Text> Subject + am|is|are + v-ing.{"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I am eating. {"\n"}                  We are running. {"\n"}                  She is playing. {"\n"}                  The cow is eating. etc. {"\n"}{"\n"}
      <Text style={{ fontWeight: 'bold' }}>3. Present Perfect Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure : </Text> Subject + have + v3.{"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I have eaten. {"\n"}                  You have eaten.  {"\n"}                  Ram have eaten. {"\n"}                 ...etc. {"\n"}{"\n"}
      <Text style={{ fontWeight: 'bold' }}>4. Present Perfect Continuous Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure : </Text> Subject + have + been + v-ing.{"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I have been eating. {"\n"}                  You have been eating.  {"\n"}                  Ram has been eating. {"\n"}                 ...etc. {"\n"}{"\n"}
          </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Past Tense</Text>
        <Text style={[styles.text, { color: colors.text }]}>
      1. Simple Past Tense {"\n"}
      2. Past Continuous Tense {"\n"}
      3. Past Perfect Tense {"\n"}
      4. Past Perfect Continuous Tense {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>1. Simple Past Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure : </Text> Subject + simple past form(v2).{"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I was very happy. {"\n"}                  You were very happy.  {"\n"}                  Ram has a horse. {"\n"}                  Ram went there. {"\n"}                  He wrote book. {'\n'}                  ...etc. {"\n"}{"\n"}
      <Text style={{ fontWeight: 'bold' }}>2. Past Continuous Tense or {"\n"}Past Progressive Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure : </Text> Subject + was/were + v-ing.{"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I was eating. {"\n"}                  You were eating.  {"\n"}                  Ram was {'\n'}                  eating. ...etc. {"\n"}{"\n"}
      <Text style={{ fontWeight: 'bold' }}>3. Past Perfect Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure : </Text> Subject + had + v3.{"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I had eaten. {"\n"}                  You had eaten.  {"\n"}                  Ram had eaten. {"\n"}                  ...etc. {"\n"}{"\n"}
      <Text style={{ fontWeight: 'bold' }}>4. Past Perfect Continuous Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure : </Text> Subject + had + been + v-ing.{"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I had been eating. {"\n"}                  You had been eating.  {"\n"}                  Ram had been eating. {"\n"}                 ...etc. {"\n"}{"\n"}
          </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Future Tense</Text>
        <Text style={[styles.text, { color: colors.text }]}>
      1. Simple Future Tense {"\n"}
      2. Future Continuous  Tense {"\n"}
      3. Future Perfect Tense {"\n"}
      4. Future Perfect Continuous Tense {"\n"}  {"\n"}
      
      <Text style={{ fontWeight: 'bold' }}>1. Simple Future Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure : </Text> Subject + Shall/Will + v1.{"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I Shall eat. {"\n"}                  You will eat.  {"\n"}                  Ram Will eat. {"\n"}                  ...etc. {"\n"}{"\n"}
      <Text style={{ fontWeight: 'bold' }}>2. Future Continuous Tense or {"\n"}Future Progressive Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure : </Text> Subject + Shall/Will + be + v-ing.{"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I Shall be eating. {"\n"}                  You will be eating.  {"\n"}                  Ram Will be eating. {"\n"}                 ...etc. {"\n"}{"\n"}
      <Text style={{ fontWeight: 'bold' }}>3. Future Perfect Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure : </Text> Subject + Shall/Will + have + v3.{"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I Shall have eaten. {"\n"}                  You will have eaten.  {"\n"}                  Ram Will have eaten. {"\n"}                 ...etc. {"\n"}{"\n"}
      <Text style={{ fontWeight: 'bold' }}>4. Future Perfect Continuous Tense</Text> {"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Structure : </Text> Subject + Shall/Will + have + been + v-ing.{"\n"}  {"\n"}
      <Text style={{ fontWeight: 'bold' }}>Example : </Text>I Shall have been eating. {"\n"}                  You will have been eating.  {"\n"}                  Ram Will have been eating. {"\n"}                 ...etc. {"\n"}{"\n"}
          </Text>
      </View>
    </ScrollView>
  );
};

export default Tense;
