import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { useTheme } from "@react-navigation/native";
import { styles } from "./Style";

const Adverb = ({ navigation }) => {
  const { colors } = useTheme();
  return (
    <ScrollView>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Adverbs</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          An adverb is a word that add quality in a verb, an adjective or other adverb. {"\n"} {"\n"}
          <Text style={{ fontWeight: 'bold' }}>Example : </Text>He is walking <Text style={{ fontWeight: 'bold' }}>slowly</Text>. {"\n"}                  You are <Text style={{ fontWeight: 'bold' }}>too</Text> good. {"\n"}                  I am <Text style={{ fontWeight: 'bold' }}>very</Text> rich. {"\n"} {"\n"}
          <Text style={{ fontWeight: 'bold' }}>Slowly, too, very</Text> are adverbs. Because these words are add some extra meaningful quality in <Text style={{ fontWeight: 'bold' }}>walking</Text>, <Text style={{ fontWeight: 'bold' }}>good</Text> and <Text style={{ fontWeight: 'bold' }}>rich</Text>. {"\n"} {"\n"}
          And you know <Text style={{ fontWeight: 'bold' }}>walking</Text> is a verb and <Text style={{ fontWeight: 'bold' }}>good</Text> and <Text style={{ fontWeight: 'bold' }}>rich</Text> are adjectives. {"\n"} {"\n"}
          Some popular Adverbs are <Text style={{ fontWeight: '500' }}>There, here, today, yesterday, soon, hardly, clearly, boldly, sadly... etc</Text>
        </Text>
      </View>
    </ScrollView>
  );
};

export default Adverb;
