import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { useTheme } from "@react-navigation/native";
import { styles } from "./Style";

const Gender = ({ navigation }) => {
  const { colors } = useTheme();
  return (
    <ScrollView>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Genders</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A gender show the category or class of noun or pronoun. {"\n"}  {"\n"} 
          <Text style={{ fontWeight: 'bold' }}>Example : </Text> Man, woman, dog, cat,{"\n"}                   teacher, wood ...ect. {"\n"}  {"\n"} 
          <Text style={{ fontWeight: 'bold' }}>In English we have 4 gender.</Text> {"\n"}  {"\n"}
          1. Masculine Gender {"\n"}
          2. Feminine Gender{"\n"}
          3. Common Gender{"\n"}
          4. Neuter Gender{"\n"}
          </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Masculine Gender</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          The Masculine gender denotes a male. {"\n"}{"\n"}
          <Text style={{ fontWeight: 'bold' }}>Example : </Text>Ram, Shyam, man, dog,{"\n"}                  father, bull, actor, king ...etc.
        </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Feminine Gender</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          The Feminine gender denotes a female. {"\n"}{"\n"}
          <Text style={{ fontWeight: 'bold' }}>Example : </Text>Sita, Radha, woman, bitch,{"\n"}                  mother, cow, actress, {"\n"}                  queen ...etc.
        </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Common Gender</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          The Common gender denotes that noun is either a male or a female . {"\n"}{"\n"}
          In common gender we have no idea that noun is a male or a female.{"\n"}{"\n"}
          <Text style={{ fontWeight: 'bold' }}>Example : </Text>Teacher, Student, Person,{"\n"}                  Animal, Doctor, Engineer, {"\n"}                  Child ...etc.
        </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Neuter Gender</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          The neuter gender denotes that noun is neither a male or nor a female . {"\n"}{"\n"}
          <Text style={{ fontWeight: 'bold' }}>Example : </Text>wood, oil, crow,{"\n"}                  book, pen, watch, {"\n"}                  school ...etc. {"\n"}{"\n"}
          All the <Text style={{ fontWeight: 'bold' }}>material, collective </Text>and <Text style={{ fontWeight: 'bold' }}> abstract</Text> nouns are neuter gender.
        </Text>
      </View>
    </ScrollView>
  );
};

export default Gender;
