import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { useTheme } from "@react-navigation/native";
import { styles } from "./Style";
const Sentences = ({ navigation }) => {
  const { colors } = useTheme();
  return (
    <ScrollView>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Sentences</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          Group of words that give meaning and arrange orderly is called
          sentence. {"\n"} {"\n"}
          <Text style={{ fontWeight: 'bold' }}>Example : </Text>We have following words : {"\n"}This a , cat , is {"\n"}
          {"\n"}Using these words we can make a sentence. {"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>This is a cat.</Text> {"\n"}{" "}
          {"\n"}
          Remember : All Sentences first letter should be capital. {"\n"}
          {"\n"}And the sentences end with full stop(.).
          {"\n"} {"\n"}
          Following are some examples : {"\n"} {"\n"}
          <Text style={{ fontWeight: "bold" }}>T</Text>his is an ox
          <Text style={{ fontWeight: "bold" }}>.</Text>
          {"\n"}
          <Text style={{ fontWeight: "bold" }}>H</Text>e is a good boy
          <Text style={{ fontWeight: "bold" }}>.</Text>
        </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Exercise Time</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          1. Make a sentence using following words. {"\n"} {"\n"}
          (i) I, a, girl, am  {"\n"}(ii) He, playing is cricket. {"\n"}(iii) mouse, the fast running fast. {"\n"}{"\n"}
          2. Correct the following sentences. {"\n"} {"\n"}
          (i) He are my friends. {"\n"}
          (ii) i am a good {"\n"}
          (iii) that cool is 
        </Text>
      </View>

      {/* <TouchableOpacity
        activeOpacity={0.8}
        onPress={() => navigation.navigate("Sentences")}
      >
        <View style={styles.nextCon}>
          <Text style={styles.next}>Sentences</Text>
        </View>
      </TouchableOpacity> */}
    </ScrollView>
  );
};

export default Sentences;
