import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  FlatList,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { StatusBar } from "expo-status-bar";
import Letter from "./Basic/Letter";
import Words from "./Basic/Words";
import Sentences from "./Basic/Sentences";
import PartOfSpeech from "./Basic/PartOfSpeech";
import Noun from "./Basic/Noun";
import Pronoun from "./Basic/Pronoun";
import Adjective from "./Basic/Adjective";
import Verb from "./Basic/Verb";
import Adverb from "./Basic/Adverb";
import Preposition from "./Basic/Preposition";
import Conjunction from "./Basic/Conjunction";
import Interjection from "./Basic/Interjection";
import NumberGrammer from "./Basic/NumberGrammer";
import Gender from "./Basic/Gender";
import Person from "./Basic/Person";
import Tense from "./Basic/Tense";
import {
  AdMobBanner,
  AdMobInterstitial,
  PublisherBanner,
  AdMobRewarded,
  setTestDeviceIDAsync,
} from "expo-ads-admob";

const Basic = ({ navigation }) => {
  return (
    <>
      {/* top heading sliding */}
      <FlatList
        data={[""]}
        renderItem={() => {
          return (
            <View style={styles.topSliderCon}>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Letter")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Letter</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Words")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Words</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Sentences")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Sentences</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Part Of Speech")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Part of Speech</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Noun")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Noun</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Pronouns")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Pronouns</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Adjective")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Adjective</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Verb")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Verb</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Adverb")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Adverb</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Preposition")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Preposition</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Conjunction")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Conjunction</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Interjection")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Interjection</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Number")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Number</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Gender")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Gender</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Person")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Person</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => navigation.navigate("Tense")}
              >
                <View style={styles.topSliderChild}>
                  <Text style={styles.topSliderChildText}>Tense</Text>
                </View>
              </TouchableOpacity>
            </View>
          );
        }}
        horizontal
        showsHorizontalScrollIndicator={false}
      />
      <ScrollView>
        <StatusBar style="light" />
        <Letter />
        <Words />
        <Sentences />
        <PartOfSpeech />
        <Noun />
        <Pronoun />
        <Adjective />
        <Verb />
        <Adverb />
        <Preposition />
        <Conjunction />
        <Interjection />
        <NumberGrammer />
        <Gender />
        <Person />
        <Tense />
      </ScrollView>
    </>
  );
};

export default Basic;

const styles = StyleSheet.create({
  topSliderCon: {
    height: 50,
    width: "100%",
    flexDirection: "row",
    justifyContent: "flex-start",
    alignItems: "center",
  },
  topSliderChild: {
    backgroundColor: "#ff4e5a",
    paddingHorizontal: 12,
    paddingVertical: 7,
    borderRadius: 5,
    transform: [{ scale: 0.9 }],
  },
  topSliderChildText: {
    color: "white",
    fontWeight: "bold",
    fontSize: 15,
  },
});
