import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TouchableOpacity,
} from "react-native";
import React from "react";
import { useTheme } from "@react-navigation/native";
import { styles } from "./Style";

const Person = ({ navigation }) => {
  const { colors } = useTheme();
  return (
    <ScrollView>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Person</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A person helps us to identify which person is first, second or third.  {"\n"}  {"\n"} 
          <Text style={{ fontWeight: 'bold' }}>Example : </Text> I, my, you, he, that{"\n"}                   their, him ...ect. {"\n"}  {"\n"} 
          <Text style={{ fontWeight: 'bold' }}>In English we have 3 Person.</Text> {"\n"}  {"\n"}
          1. First Person {"\n"}
          2. Second Person{"\n"}
          3. Third Person
          </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>First Person</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A person who is speakig is called first person. {"\n"}  {"\n"} 
          <Text style={{ fontWeight: 'bold' }}>Example : </Text> I, we, my, me,mine, myself,{"\n"}                   us, our, ourselves...etc.
          </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Second Person</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A person who is speaking with you is called second person. {"\n"}  {"\n"} 
          <Text style={{ fontWeight: 'bold' }}>Example : </Text>You, your, yours, {"\n"}                  yourself, yourselves...etc                 
          </Text>
      </View>
      <View style={[styles.card, { backgroundColor: colors.card }]}>
        <Text style={styles.header}>Third Person</Text>
        <Text style={[styles.text, { color: colors.text }]}>
          A person who spoken about is called third person. {"\n"}  {"\n"} 
          <Text style={{ fontWeight: 'bold' }}>Example : </Text>He, Him, His, she, her, it, {"\n"}                  they, them, that, this, {"\n"}                  Ram , Shyam, Sita ...etc. {"\n"}  {"\n"} 
          Sometimes third person changed according to the situation. {"\n"}  {"\n"}
          <Text style={{ fontWeight: 'bold' }}>Ram</Text>, How are you?{"\n"}{"\n"}
          According to defination <Text style={{ fontWeight: 'bold' }}>Ram</Text> is a third person. But in this sntence <Text style={{ fontWeight: 'bold' }}>Ram</Text> is a second person.
          </Text>
      </View>
    </ScrollView>
  );
};

export default Person;
